@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#main-page-container{
    width: 100vw;
    height: 100vh;
    margin:0;
    display: grid;
    max-height: 100vh;
    min-width: 350px;
}

@media screen and (min-width: 1000px) {
    #main-page-container{
        grid-template-columns: 90px 550px 1fr;
    }
  }
  @media screen and (max-width: 1000px) {
    #main-page-container{
        display: flex;
        flex-direction: column-reverse;
    }
  }
@media screen and (min-width: 1000px) {
    #side-inner{
        display: grid;
        grid-template-rows: 1fr 1fr;
        width: 100%;
        align-items: center;
        justify-items: center;
        height: 160px;
        margin-top: 65px;
        grid-row-gap: 1px;
    }
    .icon-cont-up{
        border-top: 1px solid #3498db;
    }
    .icon-cont-down{
        border-bottom: 1px solid #3498db;
    }
  }
  @media screen and (max-width: 1000px) {
    #side-inner{
        display: grid;
        grid-template-columns: 1fr 1fr;
        width: 40%;
        align-items: center;
        justify-items: center;
        height: 100%;
    }
    #side-bar{
      height: 10vh;
      min-height: 80px;
      width: 100%;
      margin: 0;
        }
       
  }
#side-bar{
    background-color: #2980b9;
    display: grid;
    justify-items: center;
    }

.icon{
    height: 40px;
    width: 40px;
    color: #063f64;
}
.icon-cont{
    width: 100%;
    height: 100%;
    display: grid;
    align-items: center;
    justify-items: center;
 
    cursor: pointer;

}

.icon-clicked{
   
    background-color: #3498db;

}
.icon-cont:hover{
    background-color: #3498db;
    
}
#list-container {
  width: 100%;
  height: 100%;
  
}
@media screen and (min-width: 1000px) {
#list-container {
  border-right: 2px solid #8b9296;
  display: grid;
  grid-template-rows: 66px 1fr;
}
}
@media screen and (max-width: 1000px) {
  #list-container {
    border-right: 2px solid #8b9296;
    display: flex;
    flex-direction: column;
  }
  }
#list-top {
  width: 100%;
  height: 65px;
  background-color: #ecf0f1;
  border-bottom: 1px solid #a5acb1d5;
  display: grid;
  align-items: center;
  justify-items: center;
}

#top-inner {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 3fr;
  width: 90%;
  height: 80%;
}

#search {
  width: 85%;
  height: 60%;
  border-radius: 8px;
  outline: none;

  border: 1px solid #cccccc;
  border-radius: 5px;
  padding-left: 8%;
}

.header-title {
  font-family: "Montserrat", sans-serif;
  font-size: 1.3em;
  font-weight: 500;
  text-align: start;
  text-transform: capitalize;
}

.search-cont {
  display: grid;
  align-items: center;
  justify-items: end;
  width: 100%;
  height: 100%;
  position: relative;
}
.title-cont {
  display: grid;
  align-items: center;
  justify-items: start;
  width: 100%;
  height: 100%;
}

.searchIcon {
    position: absolute;
    width: 16.5px;
    height: 16.5px;
    top: 18px;
    color: rgb(194, 195, 197);
  }
@media screen and (min-width: 1000px) {
  .searchIcon {
    left: 32px;
  }
}
@media screen and (max-width: 1000px) and (min-width: 800px) {
  .searchIcon {
    left: 60px;
  }
}
@media screen and (max-width: 800px) and (min-width: 600px) {
    .searchIcon {
      left: 40px;
    }
  }
  @media screen and (max-width: 600px) {
    .searchIcon {
      display: none;
    }
  }
#search-butt {
  border: 1px solid #2881b8;
  color: #2881b8;
  font-size: 1em;
  height: 65%;
  width: 90%;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
}
#search-butt:hover {
  background-color: #2881b8;
  color: #ecf0f1;
}
.search-butt-cont {
  display: grid;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 1000px) {
  #list-results {
    height: calc(100vh - 66px);
    flex-direction: column;
  }
  .list-item  {
    width: 90%;
    height: 60px;
    max-height: 100px;

  }
}
@media screen and (max-width: 1000px) and (min-width: 800px) {
  #list-results {
    height: calc(100% - 66px);
    flex-direction: row;
  }
  .list-item {
    width: 40vw;
    height: 300px;
    margin-left: 15px;
  }
}
@media screen and (max-width: 800px) {
    #list-results {
      height: calc(100% - 66px);
      flex-direction: row;
    }
    .list-item {
      width: 50vw;
      height: 300px;
      margin-left: 15px;
    }
  }
#list-results {
  align-items: center;
  width: 100%;
  display: flex;
  flex: 6 1;
  min-height: 0;
  overflow-y: scroll;
  flex: 1 0 auto;
}
.list-item {
  flex: 1 0 auto;
  margin-top: 5px;
  margin-bottom: 5px;
  min-height: 100px;
  border: solid 1px #d7dfe48f;
  border-radius: 8px;
  display: grid;
  justify-items: center;
  align-items: center;
  text-transform: capitalize;
}
@media screen and (min-width: 1000px) {
  .list-item {
    cursor: pointer;
  }
}
.list-item:hover {
  background-color: #d7dfe48f;
}
.result-name {
  font-family: "Montserrat", sans-serif;
  font-size: 1.4em;
  font-weight: bold;
  color: #4f4e4e;
}

.result-inner {
  width: 80%;
  height: 60%;
  align-items: flex-start;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

#detail-container {
  width: 100%;
  height: 100%;

  background-color: #d7dfe48f;
  display: grid;
  align-items: center;
  justify-items: center;
}
#icon-detail {
  font-family: "Nunito", sans-serif;
  color: #32343588;
}
@media screen and (min-width: 1300px) {
  #icon-detail {
    font-size: 1.5em;
  }
}
@media screen and (min-width: 1000px) and (max-width: 1300px) {
  #icon-detail {
    font-size: 1.2em;
  }
}
@media screen and (max-width: 1000px) {
  #icon-detail {
    font-size: 0.8em;
  }
  #detail-container {
    display: none;
  }
}
#no-data-cont {
  display: grid;
  grid-template-rows: 260px 1fr;
  align-items: center;
  justify-items: center;
  grid-row-gap: 10%;
  margin-bottom: 20%;
}
#data-cont {
  width: 90%;
  height: 50%;
  min-height: 300px;
  background-color: white;
  border-radius: 5px;
  border: solid 1px rgb(196, 196, 196);
}
.details-data-row {
  font-family: "Montserrat", sans-serif;
  font-size: 1.4em;
  color: #4f4e4e;
  display: block;
  margin-top: 10px;
  margin-left: 30px;
}
.big-size-row {
  font-size: 1.4em;
  font-weight: bolder;
}
.small-size-row {
  font-size: 0.8em;
}
.name-size-row {
  font-size: 1.8em;
  font-weight: bold;
}
#details-close-row{
    width: calc(100% - 10px);
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
}
#details-close{
    font-size: 1.8em;
    cursor: pointer;

}
